import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import request from '../utils/request'
interface TablaProps {
	nombreTabla?: string,
	API?: string,
}
interface tabla {
	columnas: string[],
	filas: Array<string[]>
}
const Tabla: React.FC<TablaProps> = (props) => {
	const API = props.API || 'https://equiporojoapi.herokuapp.com/tabla'
	const params = useParams<TablaProps>()

	// para usar los parametros del link o las propiedades del elemento JSX
	const nombreTabla = params.nombreTabla || props.nombreTabla
	const miTabla: tabla = {
		columnas: ['estos', 'son', 'datos de prueba'],
		filas: [['si', 'estas', 'viendo esto'], ['algo', 'salio', 'mal']]
	}
	const [tabla, setTabla] = useState(miTabla)
	const [id, setId] = useState('')
	const [ci, setCi] = useState('')
	const [nombre, setNombre] = useState('')
	const [apellido, setApellido] = useState('')
	const [fecha, setFecha] = useState('')
	const [telefono, setTelefono] = useState('')
	const [text, setText] = useState('')

	function getTabla() {
		request(`${API}/${nombreTabla}`)
			.then(data => setTabla(data))
			.catch(error => console.log(error))
		console.log('getTabla executed!')
	}
	useEffect(() => {
		getTabla()
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [props])

	function handleEdit(id: number, index: number) {
		setId(tabla.filas[index][0])
		setCi(tabla.filas[index][1])
		setNombre(tabla.filas[index][2])
		setApellido(tabla.filas[index][3])
		setFecha(tabla.filas[index][4])
		setTelefono(tabla.filas[index][5])
	}

	function handleDelete(id: number) {
		console.log(id)
	}
	const handleActualizar = () => {
		fetch(`${API}/update`, {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json',
			},
			body: JSON.stringify({
				'id': id,
				'ci': ci,
				'nombre': nombre,
				'apellidos': apellido,
				'fecha': fecha,
				'telefono': telefono
			}),
		})
			.then(res => res.json()) // check with res.ok
			.then(data => {
				setText(data.msg)
			})
	}

	return (
		<>
			<h3 style={styles.titulo}>Tabla {nombreTabla}</h3>
			<div style={styles.tabla}>
				<button onClick={() => getTabla()}>Actualizar</button>
				<table className="table table-hover table-secondary">
					<thead>
						<tr className="table-primary">
							{
								tabla.columnas.map((x, i) => (
									<th scope="col" key={i}>{x}</th>
								))
							}
							<th>opciones</th>
						</tr>
					</thead>
					<tbody>
						{
							tabla.filas.map((fila, i) => (
								<tr key={i}>
									{
										fila.map((celda, i) => (
											<td key={i}>{celda}</td>
										))
									}
									<td style={styles.td}>
										<div style={styles.containerOpciones}>
											<button className="arrow"></button>
											<div style={styles.dropdown}>
												<button className="btn btn-warning" style={{ color: 'black', fontWeight: 700 }}
													onClick={() => handleEdit(parseInt(fila[0]), i)}>editar</button>
												<button className="btn btn-danger" style={{ color: 'black', fontWeight: 700 }}
													onClick={() => handleDelete(parseInt(fila[0]))}>eliminar</button>
											</div>
										</div>
									</td>
								</tr>
							))
						}
					</tbody>
				</table>
			</div>
			<div style={{ ...styles.tabla, height: '28vh' }}>
				<h3 style={styles.titulo}>Editar</h3>
				<table className="table table-hover table-secondary">
					<thead>
						<tr className="table-primary">
							{
								tabla.columnas.map((x, i) => (
									<th scope="col" key={i}>{x}</th>
								))
							}
						</tr>
					</thead>
					<tbody>
						<tr>
							<td>
								<input type="text" value={id}
									readOnly />
							</td>
							<td>
								<input type="text" value={ci}
									onChange={e => setCi(e.target.value)} />
							</td>
							<td>
								<input type="text" value={nombre}
									onChange={e => setNombre(e.target.value)} />
							</td>
							<td>
								<input type="text" value={apellido}
									onChange={e => setApellido(e.target.value)} />
							</td>
							<td>
								<input type="text" value={fecha}
									onChange={e => setFecha(e.target.value)} />
							</td>
							<td>
								<input type="text" value={telefono}
									onChange={e => setTelefono(e.target.value)} />
							</td>
						</tr>
					</tbody>
				</table>
			</div>
			<div style={styles.logs}>
				<button className="btn btn-success"
					onClick={() => handleActualizar()}>Actualizar</button>
				<span>{text}</span>
			</div>
		</>
	)
}

export default Tabla

interface StyleSheet {
	[nombre: string]: React.CSSProperties
}

const styles: StyleSheet = {
	tabla: {
		width: '80vw',
		height: '40vh',
		margin: 'auto',
		overflow: 'auto',
		marginBottom: '2vh'
	},
	titulo: {
		textAlign: 'center',
		marginTop: '5vh'
	},
	containerOpciones: {
		display: 'flex',
		alignItems: 'center',
		position: 'relative',
		justifyContent: 'space-between'
	},
	dropdown: {
		marginLeft: '10px',
		backgroundColor: '#5b6977',
		height: '90px',
		display: 'flex',
		flexDirection: 'column',
		justifyContent: 'space-around',
	},
	logs: {
		width: '80vw',
		height: '10vh',
		margin: 'auto',
	}
}
