import React from 'react'
import { PrismLight as SyntaxHighlighter } from 'react-syntax-highlighter'
import plsql from 'react-syntax-highlighter/dist/esm/languages/prism/plsql'
import prism from 'react-syntax-highlighter/dist/esm/styles/prism/prism'

SyntaxHighlighter.registerLanguage('jsx', plsql);

interface CodeBoxProps {
  code?: string
}

const CodeBox: React.FC<CodeBoxProps> = (props) => {
  return (
    <div style={styles.main}>
      <SyntaxHighlighter language="plsql" style={prism} showLineNumbers={true}
        customStyle={styles.container}>
        {props.code}
      </SyntaxHighlighter>
    </div>
  )
}

interface StyleSheet {
  [nombre: string]: React.CSSProperties
}

const styles: StyleSheet = {
  main: {
    margin: 'auto',
    height: '60vh',
    width: '95vw'
  },
  container: {
    fontSize: '17px',
    width: '100%',
    height: '100%'
  }
}

export default CodeBox