import React, { useState } from 'react'
import Tabla from './Tabla'
import request from '../utils/request'

interface TransactionsProps {

}

const Transactions: React.FC<TransactionsProps> = (props) => {
  const [nombreTxn, setNombreTxn] = useState('Uno')
  const [text, setText] = useState('aqui debería de ver las respuestas del servidor')

  const API = `https://equiporojoapi.herokuapp.com/txn/${nombreTxn}`
  console.log(nombreTxn)

  const handleBegin = () => {
    request(`${API}/begin`)
      .then(data => setText(data.msg))
      .catch(error => console.log(error))
  }
  const handleCommit = () => {
    request(`${API}/commit`)
      .then(data => setText(data.msg))
      .catch(error => console.log(error))
  }
  const handleRollback = () => {
    request(`${API}/rollback`)
      .then(data => setText(data.msg))
      .catch(error => console.log(error))
  }

  return (
    <>
      <div style={styles.main}>
        <h1>Transacción: isolation level repeatable read</h1>

        <select id="nombreTxn" style={{ paddingLeft: '20px', marginLeft: '20px' }}
          onChange={(e) => setNombreTxn(e.target.value)}>
          <option value="Uno">Uno</option>
          <option value="Dos">Dos</option>
        </select>
        <div style={styles.buttons}>
          <button className="btn btn-info"
            onClick={() => handleBegin()}>Begin</button>
          <button className="btn btn-success"
            onClick={() => handleCommit()}>Commit</button>
          <button className="btn btn-danger"
            onClick={() => handleRollback()}>Rollback</button>
        </div>
      </div>
      <Tabla nombreTabla="persona" API={API} />
      <div style={styles.logs}>
        <h1>Logs: </h1>
        <span>{text}</span>
      </div>

    </>

  )
}

interface StyleSheet {
  [nombre: string]: React.CSSProperties
}

const styles: StyleSheet = {
  main: {
    padding: '15px',
  },
  buttons: {
    marginTop: '2vh',
    marginLeft: '10vw',
    width: '40vw',
    display: 'flex',
    justifyContent: 'space-around',
  },
  logs: {
    width: '80vw',
    height: '20vh',
    margin: 'auto',
  }
}

export default Transactions