const scripts = {
  nuevoPiloto: `CREATE OR REPLACE FUNCTION 
	nuevoPiloto(_ci int, _nombre varchar, _apellidos varchar, _fecha_de_nacimiento date, telefono int,
			   _licencia varchar, _horas_de_vuelo int, _empresa_proveedora varchar) RETURNS void 
AS $$
	DECLARE 
		nuevo_id int;
	BEGIN
		-- guardamos en la tabla persona
		insert into persona(ci, nombre, apellidos, fecha_de_nacimiento, telefono)
			values(_ci, _nombre, _apellidos, _fecha_de_nacimiento, telefono) returning id into nuevo_id;
		-- guardamos en la tabla piloto los datos restantes y con el id generado	
		insert into piloto(id, licencia, horas_de_vuelo, empresa_proveedora)
			values(nuevo_id, _licencia, _horas_de_vuelo, _empresa_proveedora);
	END;
$$ LANGUAGE plpgsql;`,
  nuevoEmpleado: `CREATE OR REPLACE FUNCTION 
	nuevoEmpleado(_ci int, _nombre varchar, _apellidos varchar, _fecha_de_nacimiento date, telefono int,
				  _codigo varchar, _categoria varchar, _turno varchar, _fecha_de_alta date, _salario int) RETURNS void 
AS $$
	DECLARE 
		nuevo_id int;
	BEGIN
		-- guardamos en la tabla persona
		insert into persona(ci, nombre, apellidos, fecha_de_nacimiento, telefono)
			values(_ci, _nombre, _apellidos, _fecha_de_nacimiento, telefono) returning id into nuevo_id;
		-- guardamos en la tabla empleado los datos restantes y con el id generado	
		insert into empleado(id, codigo, categoria, turno, fecha_de_alta, salario)
			values(nuevo_id, _codigo, _categoria, _turno, _fecha_de_alta, _salario);
	END;
$$ LANGUAGE plpgsql;`,
}

export default scripts