import CodeBox from './CodeBox'
import scripts from './sqlTexts'
const Docs = () => {
  return (
    <div style={styles.main}>
      <h3>Documentación de las funciones de postgres utilizadas y tal vez las transacciones y sus niveles</h3>

      <span style={styles.functionTitle}>Función: nuevoEmpleado</span>
      <CodeBox code={scripts.nuevoEmpleado} />

      <span style={styles.functionTitle}>Función: nuevoPiloto</span>
      <CodeBox code={scripts.nuevoPiloto} />
    </div>
  )
}

interface StyleSheet {
  [nombre: string]: React.CSSProperties
}

const styles: StyleSheet = {
  main: {
    padding: '15px'
  },
  functionTitle: {
    display: 'block',
    marginTop: '4vh',
    fontWeight: 'bold',
  }
}
export default Docs